.header-Testimonial {
    background-size: cover;
    background-position: center center;
    background-image: url(../imgs/imgTesti.jpg);
}

.background-Testimonial-dark {
    padding-bottom: 50px;
    background: rgba(0, 0, 0, 0.4);
}

.titel-Testimonial-header {
    color: #fff;
    padding: 80px 0px;
    text-align: center;
}

.titel-Testimonial-header h1 {
    font-size: 60px;
}

.titel-Testimonial-header p {
    line-height: 2;
}


/* ----------------------------------section1 */

.box-about-section1 {
    padding: 80px 0px;
}

.box-about-section1-top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

.text-p-about {
    width: 66%;
}

.text-p-about p {
    color: #7a7a7a;
    line-height: 2;
}

.titil-p-about {
    width: 26%;
}

.titil-h1-about h1 {
    font-size: 52px;
    font-weight: normal;
    margin: 0;
}

.box-about-section1-bottom {
    padding-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

.about-section1-bottom-left {
    width: 66%;
}

.about-section1-bottom-left img {
    width: 100%;
    height: 600px;
}

.about-section1-bottom-right {
    width: 28%;
}

.boxs-story>div {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
}

.icon-box {
    padding: 10px;
    background: #000;
    border-radius: 31px;
    margin: 10px;
}

.icon-box svg {
    color: #fff;
    font-size: 27px;
}

.box-border {
    border-bottom: 1px solid #e9ecef;
    padding-bottom: 30px;
}

.box-border span {
    line-height: 1.8;
    color: #7a7a7a;
}


/* ----------------------------------section1 end */


/* ----------------------------------section2     */

.box-About-section2 {
    color: #fff;
    position: relative;
    padding-top: 50px 0px;
    background-color: #1a1a1a;
}

.box-About-section2 .box-dark-1 {
    padding: 114px 0px;
    position: absolute;
    top: -180px;
    left: 0;
    width: 100%;
    z-index: -1;
    background-color: #1a1a1a;
}

.box-about-section2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: flex-start;
    padding-bottom: 50px;
}

.box-about-section2 .box-sect-1 {
    width: 30%;
}

.box-about-section2 .box-sect-2,
.box-about-section2 .box-sect-3 {
    width: 25%;
    text-align: center;
}

.box-about-section2>div img {
    width: 100%;
}

.box-sect-1 h1 {
    font-size: 43px;
}

.box-sect-1 p {
    line-height: 2;
}

.box-sect-1 button {
    background: none;
    border: 1px solid #fff;
    padding: 10px 30px;
    color: #fff;
    margin-top: 20px;
    font-size: 13px;
    font-weight: 600;
    letter-spacing: 1px;
}


/* ----------------------------------section2 end */


/* ----------------------------------section3     */

.box-About-section3 {
    background-color: #e9ecef;
    padding: 80px 0px;
}

.top-about-section3 {
    padding-bottom: 57px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}

.top-about-section3>div {
    width: 500px;
}

.top-about-section3 h1 {
    font-size: 39px;
    margin: 0;
}

.top-about-section3 p {
    color: #7a7a7a;
    line-height: 1.8;
    margin: 0;
}

.button-about-section3 {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.button-about-section3>div {
    text-align: center;
    width: calc(100% / 3);
}

.button-about-section3>div img {
    width: 100%;
    height: 500px;
}

.button-about-section3>div h2 {
    font-weight: normal;
}

.button-about-section3>div svg {
    margin: 5px;
    margin-top: 0;
    font-size: 18px;
    color: #a7a7a7;
}


/* ----------------------------------section3 end */

@media (max-width: 900px) {
    .box-about-section1-top {
        flex-direction: column;
        align-items: flex-start;
        padding: 0px 20px;
    }
    .text-p-about {
        margin-top: 30px;
        width: 90%;
    }
    .text-p-about p {
        line-height: 1.8;
        margin: 0;
        font-size: 12pz;
    }
    .box-about-section1-bottom {
        flex-direction: column;
    }
    .about-section1-bottom-right {
        width: 61%;
    }
    .about-section1-bottom-left {
        width: 100%;
        text-align: center;
    }
    .about-section1-bottom-left img {
        width: 90%;
    }
    .box-about-section2 .box-sect-1 {
        width: 80%;
        margin-bottom: 48px;
        text-align: center;
    }
    .box-about-section2 .box-sect-2,
    .box-about-section2 .box-sect-3 {
        width: 34%;
    }
    .top-about-section3>div {
        width: 100%;
        padding-top: 22px;
    }
    .button-about-section3>div img {
        height: 372px;
    }
}

@media (max-width: 700px) {
    .box-about-section1-top {
        padding: 0px 35px;
    }
    .boxs-story>div {
        width: 100%;
        padding: 0px 35px;
        flex-direction: column;
    }
    .about-section1-bottom-right {
        width: 85%;
    }
    .box-border {
        width: 100%;
        padding-bottom: 20px;
        margin-bottom: 15px;
    }
    .Relax .box-border {
        border-bottom: none;
    }
    .box-about-section2 .box-sect-2,
    .box-about-section2 .box-sect-3 {
        width: 51%;
        margin-bottom: 30px;
    }
    .box-about-section3 {
        padding: 0px 35px;
    }
    .button-about-section3 {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    .button-about-section3>div {
        width: 73%;
        margin-bottom: 50px;
    }
    .button-about-section3>div img {
        height: auto;
    }
}

@media (max-width: 550px) {
    .text-p-about {
        margin-top: 30px;
        width: 100%;
    }
    .about-section1-bottom-left img {
        height: auto;
    }
    .button-about-section3>div {
        width: 95%;
    }
}