* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.ModelImg {
    padding-bottom: 100px;
    background-color: #ffffff;
}

.box-img-map {
    margin-bottom: 12px;
}

.box-map {
    column-count: 3;
    -webkit-column-width: 33%;
    -moz-column-width: 33%;
    column-width: 33%;
    padding: 0 10px;
}

.img-map {
    width: 100%;
}

.img-map:hover {
    transition: ease-in-out;
    filter: opacity(0.8);
}


/* model */

.model {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000000d9;
    transition: opacity 0.4s ease, visible 0.4s ease, transform 0.4s ease-in-out;
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    overflow: hidden;
    z-index: 999;
}

.model-open {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}

.model-open svg {
    position: fixed;
    top: 10px;
    right: 10px;
    padding: 5px;
    width: 2rem;
    height: 2rem;
    color: #fff;
    cursor: pointer;
}

.img-model {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    padding: 20px 0 20px;
    margin: 0 auto;
}

@media (max-width: 990px) {
    .box-map {
        column-count: 2;
    }
}

@media (max-width: 700px) {
    .box-map {
        column-count: 1;
    }
}