.Footar {
    color: var(--color-9);
    background-color: var(--color-2);
}

.Footar .box-footar-1 {
    padding: 50px 0px 145px 0px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
}

.Footar .box-footar-1>div {
    /* width: calc(95% / 4); */
    padding: 10px 10px;
    font-size: 15px;
    line-height: 1.6;
}

.box-footar-1 .box-f-nyus {
    width: 26%;
}

.box-footar-1 .box-f-nyus>img {
    width: 39%;
    margin-top: 22px;
}

.box-footar-1 .box-f-nyus .box-icon-nyus {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    font-size: 20px;
}

.box-f-nyus .box-icon-nyus>svg {
    margin-right: 23px;
    cursor: pointer;
}


/* ------------------------------------------ */

.box-f-Location,
.box-f-Link,
.box-f-Opening {
    width: 20%;
}


/* -------------------------------------------- */

.box-footar-1 .box-f-Link .boxLink {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.box-f-Link .boxLink>a {
    color: #fff;
    text-decoration: none;
}


/* ------------------------------------------ */

.box-footar-1 .box-f-Opening p {
    display: flex;
}

.box-footar-1 .box-f-Opening p>svg {
    margin-right: 10px;
    color: var(--color-0);
}


/* -------------------------------------------- */

.box-f-nyus .box-icon-nyus>svg:hover,
.box-f-Location .p-svg:hover,
.box-f-Link .boxLink>a:hover {
    color: var(--color-0);
    transition: ease-in-out;
}


/* -------------------------------------------------- */

.box-hr {
    border-top: 1px solid #7a7a7a;
}

.box-hr .box-footar-2 {
    padding: 50px 0px;
}

.box-footar-2 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.box-footar-2 span {
    font-size: 14px;
}

@media (max-width: 1010px) {
    .Footar .box-footar-1>div {
        width: 40%;
    }
}

@media (max-width: 760px) {
    .box.box-footar-1 {
        padding-left: 26px;
        padding-bottom: 61px;
    }
    .Footar .box-footar-1>div {
        width: 95%;
        /* padding-left: 22px; */
    }
}

@media (max-width: 600px) {
    .box-footar-2 {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    .box-footar-2 span {
        margin-bottom: 11px;
    }
}