.header-About {
    background-size: cover;
    background-position: center center;
    background-image: url(../imgs/imgAb1.jpg);
}

.background-About-dark {
    padding-bottom: 50px;
    background: rgba(0, 0, 0, 0.4);
}

.titel-About-header {
    color: #fff;
    padding: 80px 0px;
    text-align: center;
}

.titel-About-header h1 {
    font-size: 60px;
}

.titel-About-header p {
    line-height: 2;
}


/* ---------------------------------- */

.box-Testimonial-section1 {
    overflow: hidden;
    padding-top: 80px;
    background-color: #e9ecef;
}

.box-Testimonial-section1-top {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}

.box-Testimonial-section1-top .box-a-top {
    width: 30%;
}

.box-Testimonial-section1-top .box-b-top {
    width: 60%;
}

.box-a-top h1 {
    color: #000;
    font-size: 45px;
    margin-top: 0;
}

.box-a-top p {
    line-height: 2;
    color: #847a7a;
}

.box-a-top span {
    color: #000;
    font-size: 22px;
    font-weight: 600;
    align-items: center;
    display: flex;
}

.box-a-top h5 {
    margin-top: 10px;
    font-weight: normal;
}

.box-a-top span svg {
    color: var(--color-1);
}

.box-b-top span svg,
.box-a-buttom span svg,
.box-b-buttom span svg {
    font-size: 27px;
    color: var(--color-1);
}

.box-b-top p {
    line-height: 2;
    font-size: 20px;
    color: #847a8d;
}

.box-a-buttom p,
.box-b-buttom p {
    line-height: 2;
    color: #847a8d;
}

.box-b-top>div {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.box-a-buttom>div,
.box-b-buttom>div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.box-b-top>div img {
    width: 7%;
}

.box-b-top>div h6,
.box-a-buttom>div h6,
.box-b-buttom>div h6 {
    margin-left: 15px;
    font-size: 13px;
    letter-spacing: 1.5px;
}

.box-a-buttom,
.box-b-buttom {
    padding-top: 80px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.box-a-buttom>div,
.box-b-buttom>div {
    width: calc(90% / 3);
}

.box-img-buttom {
    display: flex;
    align-items: center;
}

.box-img-buttom img {
    width: 20%;
    height: 10%;
}

.box-img-end {
    text-align: center;
}

.box-img-end img {
    transform: translate3d(77px, 78px, 10px);
    z-index: 1;
    width: 345px;
}


/* ----------------------------section2 */

.box-Testimonial-section2 {
    background-image: url(../imgs/imgAb2.jpg);
    padding: 60px 0px;
    background-size: cover;
    background-repeat: no-repeat;
}

.box-tes-section2 {
    color: #fff;
    text-align: center;
}

.box-tes-section2 h1,
.box-tes-section2 h2 {
    color: #fff;
    font-size: 48px;
}

.box-tes-section2 h2 {
    margin-top: 0px;
}

.box-tes-section2 p {
    line-height: 1.8;
}

.box-tes-section2 button {
    padding: 13px 41px;
    background: #0000;
    border: 2px solid #fff;
    color: #fff;
    font-size: 14px;
}

.box-tes-section2 button:hover {
    background: #fff;
    color: #000;
    transition: ease-in-out;
}

.box-tes-section2 h5 {
    margin-top: 56px;
    margin-bottom: 5px;
}


/* ---------------------------- */

@media (max-width: 1025px) {
    .box-tes-section1 {
        padding: 0px 30px;
    }
    .box-Testimonial-section1-top {
        display: flex;
        flex-direction: column;
    }
    .box-Testimonial-section1-top .box-a-top,
    .box-Testimonial-section1-top .box-b-top {
        width: 90%;
    }
    .box-a-buttom,
    .box-b-buttom {
        flex-direction: column;
    }
    .box-a-buttom>div,
    .box-b-buttom>div {
        width: 90%;
    }
}