.Nav {
    /* background: #000; */
    padding: 19px;
}

.Nav .box {
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.Nav1 {
    width: 55%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.List {
    width: 50%;
    text-align: right;
    position: relative;
}

nav a {
    color: var(--color-5);
    text-decoration: none;
}

nav a:hover {
    color: var(--color-3);
    transition: ease-in-out;
}

a.active {
    color: var(--color-0);
}

.List .Nav2 {
    width: 100%;
    background: #eee;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    position: fixed;
    left: 0;
    top: 72px;
}

.List .Nav2 a {
    width: 100%;
    padding: 10px;
    text-align: center;
    color: var(--color-3);
    background: #f9f9f9;
    border-bottom: 1px solid var(--color-9);
}

.List .Nav2 a:hover {
    color: var(--color-2);
}

.List .Nav2 a.active {
    color: var(--color-2);
}

.Logo {
    width: 30%;
}

.Logo img {
    width: 117px;
}

.List>svg {
    color: #fff;
    font-size: 24px;
    /* display: none; */
}

@media (max-width: 995px) {
    .Nav1 {
        width: 70%;
    }
}

@media (max-width: 920px) {
    .Nav1 {
        display: none;
    }
}

@media (min-width: 920px) {
    .List,
    .Nav2 {
        display: none;
    }
}