@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,400;1,300&family=Open+Sans&family=Raleway&family=Roboto:wght@100;700&family=Rubik:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,400;1,300&family=Open+Sans:wght@800&family=Raleway&family=Roboto:wght@100;700&family=Rubik:wght@300&display=swap");
* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

:root {
    --color-0: #f5c61c;
    --color-1: #fdcf28;
    --color-2: #1a1a1a;
    --color-3: #7a7a7a;
    --color-4: #e9ecef;
    --color-5: #ffffff;
    --color-6: #aaaaaa;
    --color-7: #fbfcff;
    --color-8: #bfd1ff;
    --color-9: #eaeaea;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: "Open Sans", sans-serif;
}

ul {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.box {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    /* background: #0006; */
}

@media (min-width: 768px) {
    .box {
        width: 750px;
        /* padding: 0px 40px; */
    }
}

@media (min-width: 992px) {
    .box {
        width: 970px;
    }
}

@media (min-width: 1200px) {
    .box {
        width: 1170px;
    }
}