.header-Menu {
    background-size: cover;
    background-position: center center;
    background-image: url(../imgs/img-18.jpg);
}

.background-Menu-dark {
    padding-bottom: 50px;
    background: rgba(0, 0, 0, 0.4);
}

.titel-Menu-header {
    color: #fff;
    padding: 80px 0px;
    text-align: center;
}

.titel-Menu-header h1 {
    font-size: 60px;
}

.titel-Menu-header p {
    line-height: 2;
}


/* ------------------------------------ */

.Dark {
    color: #fff;
    background-color: #1a1a1a;
    transition: background-color 0.25s ease-in-out;
}

.light {
    background-color: #e9ecef;
    transition: background-color 0.25s ease-in-out;
}

.box-dark-Mode {
    text-align: right;
    padding: 17px;
    font-size: 23px;
}

.box-dark-Mode svg {
    margin: 0px 10px;
}


/* ------------------------------------1 */

.box-menu-section1 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.boxs-img-text-sect1 {
    display: flex;
}

.box-menu-img-sect1 {
    width: 57%;
    transform: translateY(-61px);
}

.boxs-img-text-sect2,
.boxs-img-text-sect3 {
    padding-top: 50px;
}

.box-menu-img-sect2,
.box-menu-img-sect3 {
    padding-top: 50px;
    transform: translateY(10px);
}

.box-menu-img-sect1>img {
    width: 80%;
}

.box-menu-text-sect1 {
    display: flex;
    flex-direction: column;
}

.box-menu-text-sect1 h1 {
    font-size: 44px;
}

.box-menu-text-sect1 p {
    padding-right: 131px;
    margin-top: 0;
    line-height: 1.8;
    color: #7a7a7a;
}

.box-menu-text-sect1-A {
    padding-top: 30px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.box-menu-text-sect1-A>img {
    width: 16%;
    margin-right: 16px;
    padding: 12px;
    padding-top: 0;
    padding-bottom: 33px;
}

.box-menu-text-sect1-A h6 {
    margin: 0;
    font-size: 13px;
    font-weight: normal;
    letter-spacing: 3px;
}

.box-menu-text-sect1-A h2 {
    margin-top: 23px;
    margin-bottom: 7px;
}

.box-menu-text-sect1-A span {
    font-weight: bold;
}

.Dark .box-menu-text-sect1-A h6 {
    color: var(--color-1);
}

.List-menu {
    padding-top: 50px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
}

.List-menu .box-list {
    width: 27%;
    margin-bottom: 12px;
    border-bottom: 1px dashed #7a7a7a;
}

.List-menu .box-list h5 {
    letter-spacing: 2px;
    margin-bottom: 11px;
}

.List-menu .box-list span {
    font-size: 12px;
    font-weight: 600;
}

.List-menu .box-list p {
    color: #7a7a7a;
}


/* ----------------------------------- */

@media (max-width: 1030px) {
    .box-menu-text-sect1-A {
        padding-top: 30px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }
    .box-menu-text-sect1-A>img {
        width: 40%;
    }
}

@media (max-width: 992px) {
    .List-menu {
        padding-left: 26px;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: space-evenly;
    }
    .List-menu .box-list {
        width: 45%;
    }
    .box-menu-section1 {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        padding-left: 26px;
    }
    .box-menu-text-sect1 p {
        padding-right: 0px;
    }
}

@media (max-width: 770px) {
    .boxs-img-text-sect1 {
        display: flex;
        flex-direction: column;
    }
    .box-menu-img-sect1 {
        width: 73%;
        transition: 2s ease-in-out;
    }
    .box-menu-img-sect1>img {
        width: 100%;
        transition: 2s ease-in-out;
    }
    .List-menu {
        padding-bottom: 0px;
    }
    .boxs-img-text-sect2,
    .boxs-img-text-sect3 {
        padding-top: 0px;
    }
}

@media (max-width: 740px) {
    .List-menu .box-list {
        transition: 2s ease-in-out;
        width: 100%;
        margin-right: 34px;
    }
}