.header-Home {
    overflow: hidden;
    background-size: cover;
    background-position-y: -141px;
    background-image: url(../imgs/img-1.jpg);
    font-family: "Open Sans", sans-serif;
}

.background-dark {
    background: rgba(0, 0, 0, 0.7);
}

.header-Home .box-header {
    color: #ffffff;
    text-align: center;
    padding-top: 5px;
}

.header-Home .box-header h1 {
    font-size: 70px;
    line-height: 1.3;
    margin: 0px;
}

.box-header h6 {
    font-size: 14px;
    letter-spacing: 2px;
    margin: 22px;
}

.box-header h5 {
    font-size: 17px;
    line-height: 1.5;
    margin-top: 12px;
    font-weight: normal;
}

.stek {
    padding-top: 75px;
    padding-bottom: 20px;
}

.stek>div {
    width: 1px;
    height: 85px;
    background: #ffffff;
    margin: auto;
}

.header-Home .box-header span {
    font-size: 12px;
}


/* --------------section2-------------- */

.box-Home-section2 {
    padding-top: 50px;
}

.box-Home-section2 .box-section2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 70px;
}

.box-Home-section2 .box-section2>div {
    width: 45%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: flex-start;
}

.box-Home-section2 .box-section2 .left .left-box-1 h1 {
    font-size: 48px;
    line-height: 1.3;
    margin-top: 0;
}

.box-Home-section2 .box-section2 .left p,
.box-Home-section2 .box-section2 .right p {
    color: #7a7a7a;
    font-size: 16px;
    line-height: 1.7;
}

.box-Home-section2 .box-section2 .left h3,
.box-Home-section2 .box-section2 .right h3 {
    color: #1a1a1a;
    font-size: 19px;
}

.box-Home-section2 .box-section2 .left img,
.box-Home-section2 .box-section2 .right img {
    width: 100%;
}

.box-Home-section2 .box-section2 .img-margin {
    margin-top: 55px;
}

.box-section2 .left .img-5-left,
.left-box-3-right {
    transform: translateY(82px);
}

.left-box-3-right {
    display: none;
}


/* --------------section2 end-------------- */

.box-Home-section4 {
    padding: 80px 0px;
}

.box-img-section4 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
}

.box-img-section4>img {
    width: 31%;
    margin-bottom: 28px;
}


/* --------------section3 ----------------- */

.box-Home-section3 {
    padding-top: 80px;
    background: var(--color-2);
}

.box-titell-sec3 {
    width: 50%;
    margin: auto;
    color: #fff;
    text-align: center;
    position: relative;
    padding-bottom: 50px;
}

.box-titell-sec3 h5 {
    letter-spacing: 2px;
}

.box-titell-sec3 h1 {
    font-size: 46px;
}

.box-titell-sec3 p {
    line-height: 1.7;
    font-size: 16px;
}

.box-titell-sec3 h1 span::before {
    content: "";
    width: 73%;
    height: 1px;
    background: #7a7a7a;
    position: absolute;
    transform: translate(-426px, 27px);
}

.box-titell-sec3 h1 span::after {
    content: "";
    width: 73%;
    height: 1px;
    background: #7a7a7a;
    position: absolute;
    transform: translate(349px, 27px);
}

.box-Menu-home {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding-bottom: 50px;
}

.box-M-Home {
    width: 45%;
    color: #ffff;
    padding: 12px 0px;
    padding-bottom: 21px;
    letter-spacing: 2px;
    border-bottom: 1.5px dashed #7a7a7a;
}

.box-M-Home h6 {
    margin: 11px 0px;
}

.box-M-Home span {
    color: #7a7a7a;
}

.link-all-menu {
    padding: 80px 0px;
    text-align: center;
    position: relative;
}

.link-all-menu button {
    color: #fff;
    padding: 13px 45px;
    border: 2px solid #fff;
    background: #0000;
    font-size: 14px;
}

.link-all-menu button:hover {
    color: #000;
    background: #fff;
    transition: 0.4s ease-in-out;
}

.link-all-menu span::before {
    content: "";
    width: 39%;
    height: 1px;
    background: #7a7a7a;
    position: absolute;
    transform: translate(-465px, 27px);
}

.link-all-menu span::after {
    content: "";
    width: 39%;
    height: 1px;
    background: #7a7a7a;
    position: absolute;
    transform: translate(209px, 27px);
}

.box-section3-b {
    background-size: cover;
    background-position-y: -410px;
    background-image: url(../imgs/img7.jpg);
}

.background-dark-2 {
    padding: 25px 0px;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
}

.box-menu-section3-b {
    padding: 50px 0px;
}

.box-menu-section3-b h5 {
    font-weight: 600;
}

.box-menu-section3-b h1 {
    font-size: 46px;
}

.box-menu-section3-b p {
    line-height: 1.5;
}


/* --------------section3 end-------------- */


/* --------------section4 ---------------- */


/* --------------section4 end-------------- */


/* --------------section5 ---------------- */

.box-Home-section5 {
    padding-top: 80 0px;
}

.box-Meet-section5 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.box-Meet-left {
    width: 50%;
}

.box-Meet-right {
    width: 40%;
}

.box-Meet-left h1 {
    font-size: 48px;
    margin-top: 0;
}

.box-Meet-left img {
    width: 100%;
}

.box-Meet-left p {
    color: #7a7a7a;
    line-height: 1.8;
    margin-bottom: 43px;
}

.box-Meet-right img {
    width: 100%;
    margin-bottom: 80px;
}

.box-Meet-right .img6 {
    width: 80%;
    margin-bottom: 80px;
}


/* --------------section5 end-------------- */


/* --------------section6 ---------------- */

.box-Home-section6-A {
    background-image: url(../imgs/img17.jpg);
    background-size: cover;
    background-position-y: -150px;
}

.background-dark-3 {
    color: #fff;
    text-align: center;
    padding-top: 80px;
    padding-bottom: 225px;
    position: relative;
    background: rgba(0, 0, 0, 0.7);
}

.box-Say-section6 h6 {
    font-size: 14px;
    font-weight: normal;
}

.box-Say-section6 h2 {
    font-size: 45px;
}

.boxs-TheySay {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    position: absolute;
    transform: translate3d(-57px, 120px, 10px);
}

.box-they-sey {
    width: 27%;
    padding: 20px;
    background: #1a1a1a;
    border-radius: 10px;
    position: relative;
}

.box-they-sey p {
    line-height: 1.8;
}

.box-they-sey h5 {
    font-weight: normal;
    letter-spacing: 2px;
}

.box-they-sey img {
    outline: 4px solid #f5c61c;
    border-radius: 45px;
    position: absolute;
    transform: translate3d(-38px, -77px, 10px);
}

.box-Home-section6-B {
    padding-top: 200px;
    padding-bottom: 80px;
}

.box-Reservation {
    text-align: center;
    padding-top: 80px;
}

.box-Reservation h1 {
    font-size: 47px;
}

.box-Reservation p {
    line-height: 1.8;
    color: #7a7a7a;
}

.box-Reservation button {
    padding: 11px 20px;
    margin-bottom: 42px;
    border: 2px solid #000;
    font-size: 14px;
    background: #fff0;
    letter-spacing: 2px;
}

.box-Reservation button:hover {
    color: #fff;
    background: #000;
}

.box-Reservation h5 {
    letter-spacing: 2px;
    font-weight: normal;
}


/* --------------section6 end-------------- */

@media (max-width: 1200px) {
    .box-titell-sec3 h1 span::before {
        width: 53%;
        transform: translate(-252px, 27px);
    }
    .box-titell-sec3 h1 span::after {
        width: 53%;
    }
    .link-all-menu span::before {
        width: 34%;
        transform: translate(-350px, 27px);
    }
    .link-all-menu span::after {
        width: 34%;
    }
}

@media (max-width: 1120px) {
    .header-Home .box-header h1 {
        font-size: 58px;
    }
    .box-Home-section2 .box-section2 .left .left-box-1 h1 {
        font-size: 23px;
    }
    .header-Home {
        background-position-y: 0px;
    }
}

@media (max-width: 1000px) {
    .box-titell-sec3 h1 {
        font-size: 52px;
    }
    /* .link-all-menu span::before {
        width: 22%;
        transform: translate(-228px, 27px);
    }
    .link-all-menu span::after {
        width: 22%;
    } */
}

@media (max-width: 986px) {
    .header-Home .box-header h1 {
        font-size: 44px;
    }
    .stek {
        padding-top: 25px;
    }
    /* -------------------------2 */
    .link-all-menu span::before {
        width: 34%;
        transform: translate(-268px, 27px);
    }
    .boxs-TheySay {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        position: unset;
        transform: none;
        transform: translatey(85px);
    }
    .box-they-sey {
        width: 83%;
        margin-bottom: 100px;
    }
    .background-dark-3 {
        padding-bottom: 0px;
    }
    .box-Home-section6-B {
        padding-top: 0px;
        padding-bottom: 80px;
    }
    .boxs-TheySay:last-child {
        margin-bottom: 0px;
    }
}

@media (max-width: 868px) {
    .box-Meet-left,
    .box-Meet-right {
        width: 100%;
        text-align: center;
    }
    .box-Meet-left img,
    .box-Meet-right img,
    .img6 {
        width: 90% !important;
        margin-bottom: 50px !important;
    }
}

@media (max-width: 700px) {
    .header-Home {
        background-position-x: -33px;
    }
    .header-Home .box-header {
        padding-top: 71px;
    }
    .header-Home .box-header h1 {
        font-size: 30px;
    }
    .box-header h6 {
        font-size: 12px;
    }
    .box-header h5 {
        font-size: 14px;
        padding: 0px 39px;
    }
    .stek {
        padding-top: 0px;
    }
    /* ---------------------------2 */
    .box-Home-section2 .box-section2 .left img,
    .box-Home-section2 .box-section2 .right img {
        max-width: 552px;
    }
    .box-Home-section2 .box-section2 {
        padding-top: 0px;
        justify-content: center;
    }
    .box-Home-section2 .box-section2>div {
        width: 88%;
    }
    .box-Home-section2 .box-section2 .left .left-box-1 h1 {
        font-size: 30px;
    }
    .box-section2 .left .left-box-3 {
        display: none;
    }
    .left-box-3-right {
        display: block;
    }
    /* ------------------------------------3 */
    .box-titell-sec3 {
        width: 100%;
    }
    .box-titell-sec3 h1 span::before,
    .box-titell-sec3 h1 span::after,
    .link-all-menu span::before,
    .link-all-menu span::after {
        display: none;
    }
    .box-section3-b {
        background-position-y: -362px;
    }
    .box-menu-section3-b p {
        font-size: 15px;
    }
    .box-titell-sec3 h1 {
        font-size: 28px;
    }
    .box-Menu-home {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        align-content: space-around;
    }
    .box-M-Home {
        width: 91%;
    }
    .box-Home-section3 {
        padding-bottom: 0px;
    }
    .box-M-Home h6 {
        font-size: 12px;
    }
    .box-menu-section3-b {
        padding: 36px 30px;
    }
    /* ------------ */
    .box-img-section4 {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
    }
    .box-img-section4>img {
        width: 92%;
        margin-bottom: 28px;
    }
}