.container {
    position: relative;
    overflow: hidden;
}

.header-Contact {
    background-size: cover;
    background-position: center center;
    background-image: url(../imgs/img-18.jpg);
}

.background-Contact-dark {
    padding-bottom: 50px;
    background: rgba(0, 0, 0, 0.4);
}

.titel-Contact-header {
    color: #fff;
    padding: 80px 0px;
    text-align: center;
}

.titel-Contact-header h1 {
    font-size: 60px;
}

.titel-Contact-header p {
    line-height: 2;
}


/* ----------------------------------section1 */

.box-Contact-section1 {
    padding: 80px 0px;
    background: #e9ecef;
}

.box-contact-section {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.box-contact-section>h5 {
    margin-bottom: 0;
    letter-spacing: 2px;
}

.box-contact-section>h1 {
    font-size: 47px;
    margin-bottom: 0;
}

.box-contact-section>p {
    color: #7a7a7a;
    line-height: 2;
}

.box-contact-section>button {
    padding: 10px 20px;
    font-size: 13px;
    background: #0000;
    border: 1px solid #000;
    font-weight: 600;
}

.box-contact-section>button:hover {
    color: #fff;
    background: #000;
    border: 1px solid #000;
}

.box-contact-section>h6 {
    margin-top: 44px;
    margin-bottom: 0;
    letter-spacing: 2px;
    font-size: 12px;
}

.box-contact-section>h2 {
    font-size: 50px;
    margin-bottom: 13px;
    margin-top: 13px;
}

.box-contact-section>span {
    color: #7a7a7a;
}


/* ---------------------------------2 */

.box-Contact-section2 {
    padding-bottom: 80px;
    background: #e9ecef;
}

.box-r-cont-section2 {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.box-r-cont-section2 p {
    margin-top: 0;
    font-size: 17px;
    line-height: 1.5;
    color: #7a7a7a;
}

.box-r-cont-section2 h1 {
    font-size: 40px;
    color: #7a7a7a;
}

.box-r-cont-section2 form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 45%;
}

form>input,
form>textarea {
    width: 90%;
    padding: 10px;
    margin-bottom: 10px;
}

.box-r-cont-section2 form>button {
    padding: 10px 20px;
    font-size: 13px;
    background: #0000;
    border: 1px solid #000;
    font-weight: 600;
}

.box-r-cont-section2 form>button:hover {
    color: #fff;
    background: #000;
    border: 1px solid #000;
}


/* ---------------------------imgbox */

.box-imgs {
    position: absolute;
    transform: translate3d(68px, -224px, 10px);
    right: 0;
}


/* ---------------------------------- */

@media (max-width: 990px) {
    .box-r-cont-section2 form {
        width: 68%;
    }
}

@media (max-width: 800px) {
    .box-imgs img {
        width: 249px;
    }
}

@media (max-width: 760px) {
    .box-contact-section br {
        display: none;
    }
    .box-contact-section>h1 {
        font-size: 32px;
    }
    .box-contact-section>p {
        font-size: 15px;
        margin: 14px 65px;
    }
    .box-contact-section>h2 {
        font-size: 40px;
    }
}

@media (max-width: 650px) {
    .box-r-cont-section2 form {
        width: 82%;
    }
    .box-imgs img {
        width: 195px;
    }
}

@media (max-width: 550px) {
    .box-contact-section>h1 {
        font-size: 26px;
    }
    .box-contact-section>p {
        margin: 14px 42px;
    }
    .box-contact-section>h2 {
        font-size: 26px;
    }
}